/* REST vars */
.footer-content {
  position: relative;
  width: 100%;
  background-color: #fffbf8;
}
.footer-content .copyright-text {
  margin: 0 auto;
  font-weight: 500;
  text-align: center;
  line-height: 4;
  font-size: 18px;
}
.footer-content hr {
  width: 40%;
  margin: 0 auto;
  height: 3px;
  border: 0;
  border-top: 3px solid #da96b0;
  filter: drop-shadow(4px 4px #6b4554);
}
.footer-content .footer-text {
  width: 60%;
  display: grid;
  gap: 40px;
  grid-template-columns: 0.3fr 0.3fr 0.4fr;
  margin: 0 auto;
  padding: 5rem 0;
}
.footer-content .footer-text .footer-text-element {
  width: 100%;
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.footer-content .footer-text .footer-text-element h2 {
  color: #da96b0;
  font-size: 30px;
  font-family: "Bitter", sans-serif;
  line-height: 1.6;
  letter-spacing: 1px;
  filter: drop-shadow(3px 3px #6b4554);
  text-align: center;
}
.footer-content .footer-text .footer-text-element p {
  line-height: 1.8;
  font-size: 18px;
  text-align: center;
}
.footer-content #footer-menu-btn {
  border: none;
  background-color: #fdf0e6;
  cursor: pointer;
}
.footer-content #footer-menu-btn p {
  display: flex;
  gap: 10px;
  padding: 0.25rem 0;
  font-size: 18px;
  font-weight: 100;
  font-family: "Comfortaa", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: 0.5s;
}
.footer-content #footer-menu-btn :hover {
  background-color: #f8d3b4;
  margin-left: 1rem;
}

@media (max-width: 1485px) {
  .footer-content .footer-text {
    width: 60%;
  }
}
@media (max-width: 1285px) {
  .footer-content .footer-text {
    width: 70%;
  }
}
@media (max-width: 1005px) {
  .footer-content .footer-text {
    width: 80%;
    grid-template-columns: 0.3fr 0.3fr 0.4fr;
  }
  .footer-content .footer-text .footer-text-element {
    gap: 5px;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 25px;
    filter: drop-shadow(2px 2px #6b4554);
  }
  .footer-content .footer-text .footer-text-element p {
    font-size: 16px;
  }
  .footer-content #footer-menu-btn p {
    font-size: 16px;
  }
}
@media (max-width: 925px) {
  .footer-content .copyright-text {
    font-size: 16px;
  }
  .footer-content hr {
    width: 60%;
  }
  .footer-content .footer-text {
    width: 50%;
    display: flex;
    gap: 30px;
    flex-direction: column;
    padding: 3rem 0;
  }
}
@media (max-width: 770px) {
  .footer-content .copyright-text {
    font-size: 14px;
  }
  .footer-content .footer-text {
    grid-template-columns: 0.33fr 0.33fr 0.33fr;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 20px;
    filter: drop-shadow(1.5px 1.5px #6b4554);
  }
  .footer-content .footer-text .footer-text-element p {
    font-size: 14px;
  }
  .footer-content #footer-menu-btn p {
    font-size: 14px;
  }
}
@media (max-width: 700px) {
  .footer-content .footer-text {
    gap: 30px;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 20px;
  }
}
@media (max-width: 465px) {
  .footer-content .footer-text {
    width: 60%;
  }
}
@media (max-width: 375px) {
  .footer-content p {
    font-size: 12px;
  }
  .footer-content .footer-text {
    width: 70%;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 18px;
  }
  .footer-content .footer-text .footer-text-element p {
    font-size: 12px;
  }
  .footer-content #footer-menu-btn p {
    font-size: 12px;
  }
}