/* REST vars */
.banner-container {
  min-height: 14vh;
  background-color: #fdf0e6;
}
.banner-container p {
  font-size: 30px;
  font-weight: 200;
  line-height: 14vh;
  text-align: center;
  color: #444444;
  letter-spacing: 0.8px;
}
.banner-container p span {
  font-weight: 100;
  color: #da96b0;
  font-family: "Rubik Gemstones", cursive;
  filter: drop-shadow(3px 3px #6b4554);
}

@media (max-width: 1550px) {
  .banner-container p {
    font-size: 25px;
  }
}
@media (max-width: 1290px) {
  .banner-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .banner-container p {
    width: 50%;
    font-size: 20px;
    line-height: 1.6;
  }
  .banner-container p span {
    filter: drop-shadow(1.5px 1.5px #444444);
  }
}
@media (max-width: 1180px) {
  .banner-container p {
    width: 50%;
  }
}
@media (max-width: 985px) {
  .banner-container p {
    width: 60%;
  }
}
@media (max-width: 820px) {
  .banner-container p {
    width: 70%;
    font-size: 18px;
  }
  .banner-container p span {
    filter: none;
  }
}
@media (max-width: 640px) {
  .banner-container p {
    width: 80%;
  }
}
@media (max-width: 570px) {
  .banner-container p {
    font-size: 16px;
  }
}