/* REST vars */
.menu-container {
  margin: 0;
}
.menu-container .menu-icon {
  color: #f8d3b4;
  line-height: 8vh;
  padding-left: 1rem;
  padding-top: 0.5rem;
  font-size: 50px;
  filter: drop-shadow(2px 2px #e97a1d);
  cursor: pointer;
}
.menu-container .small-menu {
  height: 86vh;
  width: 100%;
  position: absolute;
  top: 14vh;
  left: 0%;
  z-index: 10;
  display: flex;
  gap: 50px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #302939;
  overflow: hidden;
}
.menu-container .small-menu .small-menu-element {
  font-size: 40px;
  padding: 0 1rem;
  color: #fdf0e6;
  filter: drop-shadow(3px 3px #e97a1d);
  font-weight: 100;
  font-family: "Rubik Gemstones", cursive;
  letter-spacing: 3px;
  text-transform: uppercase;
  border: 1px solid transparent;
  transition: 0.5s;
  cursor: pointer;
}
.menu-container .small-menu .small-menu-element:hover {
  color: #da96b0;
  transform: scale(0.85);
  filter: drop-shadow(2px 2px #6b4554);
}

@media (max-width: 695px) {
  .menu-container .small-menu .small-menu-element {
    font-size: 35px;
    filter: drop-shadow(1px 1px #e97a1d);
  }
}
@media (max-width: 445px) {
  .menu-container .small-menu .small-menu-element {
    font-size: 30px;
  }
}